* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  padding: 0.4rem;
  width: 4rem;
  background: rgb(0, 0, 0);
  color: gold;
  font-size: 1rem;
}

.main-editor {
  background: rgba(230, 230, 230, 0.295);
  display: flex;
  width: 100%;
  height: 90%;
  padding: 1rem;
}

.first {
  background-color: #ffffff;
  width: 50%;
  overflow-x: auto;
  overflow-y: auto;
  white-space: pre;
  box-shadow: 0 1px 1px rgb(22, 22, 22);
  outline: none;
  padding: 0.4rem;
  height: 100%;
}

.second {
  background-color: rgb(255, 255, 255);
  width: 50%;
  overflow-y: auto;
  white-space: pre;
  right: 0;
  box-shadow: 0 1px 1px rgb(22, 22, 22);
  padding: 0.4rem;
  height: 100%;
}
#welcome {
  height: 400px;
  background-image: url("src/ship.jpg.jpg");
  background-size: cover;
}

body {
  background-color: lightgoldenrodyellow;
  font-family: "Gill Sans", sans-serif;
  color: #333;
}

.container {
  font-weight: italic;
}
#history {
  font-weight: bold;
}
.flex-container {
  display: flex;
  justify-content: space-around;
}
